
:root {
    --main-0: #E7E9E9;
    --main-1: #FFFBFD; /* Mainly use main-1 */

    --contrast-0: #1E1B1D;
    --contrast-1: #40393D; /* Mainly use contrast-1 */
    --contrast-2: #4D4449;

    --primary-0: #E9437B;
    --primary-1: #FFA4C3;

    --secondary-0: #7BD9F9;
    --secondary-1: #ABE1F3;

    --background: var(--main-1);
    --color: var(--contrast-1);

    --font-size: 10px;
    font-size: var(--font-size);
}

body {
    background-color: var(--main-1);

    padding: 0;
    margin: 0;

    font-family: 'Montserrat', sans-serif;
    color: var(--color);

    scroll-behavior: smooth;
    overflow-x: hidden;
}

html, body, div#root, div.wrapper, main {
    width: 100vw;
}



/*
    SECTIONS
*/

section {
    display: grid;
    place-items: center;
    position: relative;
}

.hero-section .watermarks {
    transform: rotateZ(10deg) translateY(-20rem);
}

.background.dark {
    --tilt: 8rem;
    --background: var(--contrast-1);
    --color: var(--main-1);

    background-color: var(--background);
    color: var(--color);

    clip-path: polygon(0 var(--tilt), 100% 0, 100% calc(100% - var(--tilt)), 0 100%);
}

.section-inner {
    display: grid;
    place-items: center;

    width: 60%;
    height: 100%;
    box-sizing: border-box;

    padding: 8rem 5rem;
}

.section-inner.split {
    grid-template-columns: 1fr 1fr;
}

.background.dark .section-inner {
    margin: 15rem 0;
}

@media only screen and (max-width: 1000px) {
    .section-inner {
        width: auto;
    }
}

@media only screen and (max-width: 750px) {
    .section-inner {
        grid-template-columns: none;
        grid-template-rows: 1fr 1fr;
    }
    .watermarks {
        display: none;
    }
}

.watermarks {
    z-index: -1;
    width: 0;
    height: 0;

    position: absolute;
    top: 0;
    left: 0;

    user-select: none;

    --color: var(--main-0);
}

.watermarks.dark {
    --color: var(--contrast-2);
}

.watermarks > h1 {
    color: var(--color);
    font-family: 'Lora';
    font-weight: 500;
    font-size: 12rem;
    letter-spacing: 0.2em;

    position: absolute;
    margin: 0;
    width: max-content;
}

header {
    background-color: var(--contrast-1);
    /*padding: 1.5rem 3rem;*/
    padding-right: 5rem;
}

header, header nav {
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
}

header nav button {
    color: var(--main-1);
    background-color: inherit;

    padding: 1.5rem 5rem;
}

header nav button:hover {
    background-color: var(--contrast-2);
}

header h4 {
    color: var(--main-1);
    font-size: 1.7rem;
    font-weight: 400;
    letter-spacing: 1rem;

    margin: 0;
    margin-left: 2rem;

    width: max-content;
}

.header-inner-container {
    display: inline-block;
    margin-top: 3rem;
}

.header-outer-container {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    padding-top: 3rem;
    padding-right: 7rem;
}

@media only screen and (max-width: 900px) {
    .header-outer-container, .header-inner-container, .header-inner-container .paper-stack {
        width: 80vw;
        box-sizing: border-box;
    }
    
    .header-outer-container {
        padding-right: 0;
        display: flex;
        justify-content: center;
        width: 100%;
        margin-left: -2.5rem;
    }
}

ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

ul.dashed > li > *::before {
    content: "-";
    padding: 0 1.5rem;
}

button {
    background: none;
    border: none;
    outline: none;

    font-family: 'Montserrat';
    font-weight: 600;
    font-size: 1.5rem;

    cursor: pointer;
}

.paper-stack {
    transform: translate(1rem, -1rem);
    width: min-content;
    height: min-content;
    
    transition: transform 100ms ease-out;
}

.paper-stack:hover {
    transform: translate(0.8rem, -0.8rem);
}

.header-outer-container.click-enter .paper-stack, .header-outer-container.click-enter-done .paper-stack {
    transform: translate(0.5rem, -0.5rem);
}

.paper-button {
    --width: 20rem;
    --height: 4.5rem;
    --border: 3px;
    --padding-left: 3rem;
    --translate-0: translate(1.1rem, -1.3rem);
    --translate-1: translate(0.7rem, -0.9rem);
    --translate-active: translate(-3px, -3px);

    display: inline-block;
    position: relative;
    width: var(--width);
    height: var(--height);

    border: var(--border) solid var(--primary-0);
    box-sizing: border-box;

    margin-left: 1.5rem;
    margin-top: 1rem;

    cursor: pointer;
}

.paper-button button {
    width: calc(var(--width) - var(--border) * 2);
    height: calc(var(--height) - var(--border) * 2);

    background-color: var(--background);

    padding: 0;
    margin: 0;

    border: inherit;
    box-sizing: content-box;

    transform-origin: 3px;
    transform: var(--translate-0);

    transition: transform 100ms ease-out, color 50ms;
}

.paper-button span.fill {
    width: 100%;
    height: 100%;

    position: absolute;
    top: 0;
    left: 0;

    background-color: var(--primary-0);
    color: var(--color);

    transition: inherit;
}

.paper-button span.text {
    position: absolute;

    top: 0;
    bottom: 0;
    left: var(--padding-left);

    margin-top: auto;
    margin-bottom: auto;
    height: auto;

    display: grid;
    place-items: center;
    text-align: center;

    color: var(--primary-0);
    
    transition: inherit;
}

.paper-button span.fill {
    transform-origin: 0;
    transform: scaleX(0);
}

.paper-button:hover span.text {
    color: var(--main-1);
}

.paper-button:hover span.fill {
    transform: scaleX(1);
}

.paper-button:hover button {
    transform: var(--translate-1);
}

.paper-button button:active {
    transform: var(--translate-active);
}


.paper-button.primary button {
    background-color: var(--primary-0);
}

.paper-button.primary span.fill {
    background-color: var(--primary-0);
    filter: brightness(0.8);
}

.paper-button.primary span.text {
    color: var(--main-1);
}

.scroll-suggestion {
    position: fixed;
    left: 50%;
    bottom: 5rem;

    cursor: pointer;
}

.scroll-suggestion i {
    color: var(--background);
    font-size: 3.5rem;
}

span.epic {
    position: relative;
}

span.epic :nth-child(1) {
    font-size: 120%;
    color: var(--primary-0);
    cursor: pointer;
}

span.epic > span:not(:first-child) {
    font-weight: 400;
    font-size: 160%;
    filter: opacity(0.7);
    user-select: none;
    pointer-events: none;

    /* transition: transform 100ms ease-out; */
}

span.epic :nth-child(2) {
    color: var(--primary-1);

    position: absolute;
    left: -60%;
    top: -80%;
}

span.epic :nth-child(3) {
    color: var(--secondary-1);

    position: absolute;
    left: 30%;
    top: 20%;
}

.card-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    width: 100%;
}

.card {
    --background: var(--contrast-1);
    --color: var(--main-1);

    background-color: var(--background);
    color: var(--color);
    border-radius: 0.5rem;

    box-sizing: border-box;
    padding: 3rem 2rem;
    padding-bottom: 5rem;

    height: 35rem;
    width: 23rem;

    display: grid;
    grid-template-rows: 1fr 1fr;
    place-items: center;
}

.card .icon svg {
    transform: scale(2);
}

svg {
    fill: var(--color);
}

.card-section .watermarks :first-child {
    font-size: 60rem;
}

.contact-section .section-inner {
    gap: 5rem;
}

.contact-section .section-inner > div {
    place-self: start;
}

.contact-section .section-inner h2 {
    margin-top: 0;
}

.contact-section .links > div {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.contact-section .links > div svg {
    transform: scale(1.5);
    margin-right: 3rem;
}



/*
    TYPOGRAPHY
*/

h1 {
    font-size: 7rem;
    font-weight: 600;
}

h2 {
    font-size: 4rem;
    font-weight: 600;
}

h3 {
    font-size: 4rem;
    font-weight: 500;
}

h4 {
    font-size: 1.8rem;
    font-weight: 600;
    margin: 0 0;
}

p {
    font-size: 1.8rem;
    font-weight: 400;
    line-height: 150%;
}

p.centered {
    display: flex;
    align-items: center;
}